.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 44px;
  padding: 0 15px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  z-index: 100;
}
.header .back,
.header .next,
.header .next-page {
  height: 34px;
  width: 50px;
  cursor: pointer;
}
.header .back {
  background: url(img/back.png) left center no-repeat;
  background-size: 24px 24px;
}
.header .next-page {
  background: url(img/go.png) right center no-repeat;
  background-size: 24px 24px;
}
.header .title {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.headerbox {
  height: 44px;
}
